<template>
<v-ons-page>
  <h1>Smart Poker</h1>
        <p>これからテキサスホールデムを始めます。ベットするチャンスは4回以上、その都度、賭け金も上がる激熱な展開が待っています。ゲームを続けるか、降りるか、選択はあなた次第です。最終的には、決められたラウンド数で一番多くチップを持っているあなたが勝者です。</p>
  <div>
    <h2>ゲーム設定</h2>
    <form @submit.prevent="submit">
      <label for="player">プレイヤー数</label>
      <div><input type="number" v-model="player"></div>
      <label for="maxRound">ラウンド</label>
      <div><input type="number" v-model="maxRound"></div>
      <label for="chip">チップ</label>
      <div><input type="number" v-model="chip"></div>
      <label for="minbet">最低ベット</label>
      <div><input type="number" v-model="minBet"></div>
      <div>
        <button type="submit">ゲーム開始</button>
      </div>
    </form>
  </div>
  <div>
    <a href= "./ranking.html" ></a>
  </div>
  </v-ons-page>
</template>

<script>
import page2 from './page2.vue'

export default {
  data() {
    return {
      player: 4,
      maxRound: 4,
      chip: 300,
      minBet: 10,
      players: [],
      records:[]
    };
  },
  created(){
    this.fetch();
  },
  methods: {
    submit: function(){
      this.$store.state.player = Number(this.player);
      this.$store.state.maxRound = Number(this.maxRound);
      this.$store.state.chip = Number(this.chip);
      this.$store.state.minBet = Number(this.minBet);
      this.$store.commit("navigator/push", page2);
    },
    fetch() {
      let that = this;
      console.log(this.$url);
      fetch(this.$url + "/ranking", {
        method: "GET"
      })
        .then(function(response) {
          if (response.status == 200) {
            return response.json();
          }
          // 200番以外のレスポンスはエラーを投げる
          return response.json().then(function(json) {
            throw new Error(json.message);
          });
        })
        .then(function(json) {
          // レスポンスが200番で返ってきたときの処理はここに記述する
          console.log(json);
          that.records = json.records;
          console.log(that.records);
        })
        .catch(function(err) {
          // レスポンスがエラーで返ってきたときの処理はここに記述する
        });
    }
  }
};
</script>
<style scoped>

</style>



