<template>
  <v-ons-page>
    <h2>
      ラウンド{{round}}
      <br />
      {{bettingRound[br]}}
      <br />
      {{winComment}}
    </h2>
    <div>{{order}} の順です。</div>
    <div>ディーラーは{{dealer}}です。</div>
    <div>{{turn}}の番です。</div>
    <div>
      <form @submit.prevent="submit">
        <select v-model="selectActionValue">
          <option v-for="(label,id) in selectActionOptions" :value="id">{{ label }}</option>
        </select>
        <br />
        <label for="bet">ベット額</label>
        <br />
        <input type="number" v-model="pay" />
        <div>
          <button type="submit">アクション</button>
          <br />
          {{this.comment}}
        </div>
      </form>
    </div>
    <br />
    <modal name="winner-modal">
      <div style="margin-left:100px">
        <h2>ショーダウン</h2>
        <select v-model="selectWinnerValue">
          <option v-for=" (player,index) in players" :value="index+1">{{ player.name }}</option>
        </select>
        の勝ち
        <br />
        <br />
        <v-ons-button @click="winner">次のラウンドへ</v-ons-button>
      </div>
    </modal>

    <br />
    <div>ポット{{pot}}</div>
    <ul v-for="item of players" v-bind:key="players.name" style="float:left">
      <li>{{ item.name }}/{{item.isplay}}</li>
      <li>ベット : {{item.bet}}</li>
      <i>チップ: {{item.chip}}</i>
    </ul>
  </v-ons-page>
</template>

<script>
import page1 from "./page1.vue";
import page3 from "./page3.vue";

export default {
  data() {
    return {
      player: this.$store.state.player,
      maxRound: this.$store.state.maxRound,
      chip: this.$store.state.chip,
      minBet: this.$store.state.minBet,
      array: [],
      round: 1,
      dealer: "",
      pot: 0,
      bettingRound: ["プリフロップ", "フロップ", "ターン", "リバー"],
      br: 0,
      players: [],
      allBet: 0,
      order: "",
      turn: "",
      action: "",
      p: 0,
      selectActionValue: 2,
      selectActionOptions: {
        1: "ベット",
        2: "コール",
        3: "レイズ",
        4: "チェック",
        5: "フォールド",
        6: "オールイン"
      },
      selectWinnerValue: "",
      pay: 10,
      playing: 0,
      comment: "",
      winComment: "",
      checkCount: 0,
      record: [],
    };
  },
  components: {},
  created() {
    this.fetch();
    //プレイヤー設定
    for (let i = 0; i < this.player; i++) {
      let j = "P" + (i + 1);
      let addPlayer = { name: j, chip: this.chip, bet: 0, isplay: 0 };
      this.players.push(addPlayer);
    }
    //席順
    for (let i = this.players.length - 1; i >= 0; i--) {
      let rand = Math.floor(Math.random() * (i + 1));
      [this.players[i], this.players[rand]] = [
        this.players[rand],
        this.players[i]
      ];
    }
    //席順のコンソール出力
    let q = 0;
    for (let item in this.players) {
      this.order = this.order + this.players[item]["name"] + ",";
      q++;
      if (q == this.players.length) {
        console.log(this.order + "の順です。");
      }
    }
    //プリフロップ
    console.log(this.bettingRound[0]);
    //ディーラーを決定
    this.dealer = this.players[0].name;
    console.log("ディーラー:" + this.dealer);
    this.playing = this.players.length;

    this.sbbb();
  },
  conputed: {},
  methods: {
    submit: function() {
      if (this.pay < this.players[this.p % this.players.length].chip) {
        if (this.pay >= this.minBet) {
          this.winComment = "";
          //アクション
          this.action = this.selectActionOptions[this.selectActionValue];
          switch (this.action) {
            case "ベット":
              this.betting();
              break;
            case "コール":
              this.call();
              break;
            case "レイズ":
              this.raise();
              break;
            case "チェック":
              this.check();
              break;
            case "フォールド":
              this.fold();
              break;
            case "オールイン":
              this.allIn();
              break;
          }
        } else {
          this.comment = "最低ベット額は" + this.minBet + "です";
        }
      } else {
        this.comment = "所持しているチップ数を超えています";
      }
    },
    betting() {
      console.log("ベット");
      if (
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        ) == 0
      ) {
        this.checkCount = 0;
        this.players[this.p % this.players.length].bet = this.pay;
        this.players[this.p % this.players.length].chip =
          this.players[this.p % this.players.length].chip -
          this.players[this.p % this.players.length].bet;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else {
        this.comment = "既にベットされています";
      }
    },
    call() {
      console.log("コール");
      if (
        this.players[this.p % this.players.length].bet <
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        )
      ) {
        this.checkCount = 0;
        this.pay = Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        );
        let pay2 = this.pay - this.players[this.p % this.players.length].bet;
        this.players[this.p % this.players.length].bet = this.pay;
        this.players[this.p % this.players.length].chip =
          this.players[this.p % this.players.length].chip - pay2;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else {
        this.comment = "コールできません";
      }
    },
    raise() {
      console.log("レイズ");
      if (
        this.pay >=
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        )
      ) {
        this.checkCount = 0;
        this.pay =
          this.pay +
          Math.max.apply(
            null,
            this.players.map(function(o) {
              return o.bet;
            })
          );
        let pay2 = this.pay - this.players[this.p % this.players.length].bet;
        this.players[this.p % this.players.length].bet = this.pay;
        this.players[this.p % this.players.length].chip =
          this.players[this.p % this.players.length].chip - pay2;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else {
        this.comment = "ベットの2倍以上でレイズしてください";
      }
    },
    check() {
      console.log("チェック");
      if (
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        ) == 0
      ) {
        this.checkCount++;
        if (this.checkCount == this.playing) {
          this.checkCount = 0;
          this.nextBr();
        } else {
          this.nextTurn();
        }
        //プリフロップbb round+1=bb
      } else if (
        this.br == 0 &&
        this.p % this.players.length == (this.round + 1) % this.players.length
      ) {
        this.nextBr();
      } else {
        this.comment = "既にベットされています";
      }
    },
    fold() {
      console.log("フォールド");
      this.players[this.p % this.players.length].isplay = 1;
      console.log(
        this.players[this.p % this.players.length].name + ":フォールド"
      );
      this.playing--;
      this.nextTurn();
    },
    allIn() {
      console.log("オールイン");
      if (
        this.players[this.p % this.players.length].chip +
          this.players[this.p % this.players.length].bet >=
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        )
      ) {
        this.checkCount = 0;
        this.players[this.p % this.players.length].bet =
          this.players[this.p % this.players.length].bet +
          this.players[this.p % this.players.length].chip;
        this.players[this.p % this.players.length].chip = 0;
        this.players[this.p % this.players.length].isplay = 2;
        this.playing--;
        console.log(
          "ベット額:" + this.players[this.p % this.players.length].bet
        );
        console.log(
          "チップ:" + this.players[this.p % this.players.length].chip
        );
        this.nextTurn();
      } else {
        this.comment = "チップが足りません";
      }
    },
    winner() {
      if (this.selectWinnerValue != "") {
        this.selectWinnerValue = this.selectWinnerValue - 1;
        this.players[this.selectWinnerValue].chip =
          this.players[this.selectWinnerValue].chip + this.pot;
        this.winComment =
          this.players[this.selectWinnerValue].name + "さんが勝ちました";
        this.selectWinnerValue = "";
        this.$modal.hide("winner-modal");
        this.nextRound();
      }
    },
    nextTurn() {
      this.comment = "";
      //ベットが全て同じか
      let eBet = 0;
      for (let i = 0; i < this.players.length; i++) {
        if (
          this.players[this.p % this.players.length].bet == this.players[i].bet
        ) {
          eBet++;
        }
      }
      console.log(this.p + "." + this.players.length);
      if (
        eBet != this.playing ||
        Math.max.apply(
          null,
          this.players.map(function(o) {
            return o.bet;
          })
        ) == 0 ||
        (this.br == 0 && this.p == this.players.length + 1)
      ) {
        this.p++;
        //フォールドした人を飛ばす
        while (this.players[this.p % this.players.length].isplay != 0) {
          this.p++;
          if (this.p == this.maxRound + this.players.length) {
            break;
          }
        }
        //全員フォールドしたとき
        if (this.playing == 1) {
          if (
            Math.max.apply(
              null,
              this.players.map(function(o) {
                return o.isplay;
              })
            ) == 1
          ) {
            for (let i = 0; i < this.players.length; i++) {
              this.allBet = Number(this.allBet) + Number(this.players[i].bet);
              this.players[i].bet = 0;
            }
            this.pot = this.pot + Number(this.allBet);
            this.allBet = 0;
            this.players[this.p % this.players.length].chip =
              this.players[this.p % this.players.length].chip + this.pot;
            this.winComment =
              this.players[this.p % this.players.length].name +
              "さんが勝ちました";
            this.nextRound();
          } else {
            this.turn = this.players[this.p % this.players.length].name;
            console.log(this.turn + "の番");
          }
        } else if (this.playing == 0) {
          for (let i = 0; i < this.players.length; i++) {
            this.allBet = Number(this.allBet) + Number(this.players[i].bet);
            this.players[i].bet = 0;
          }
          this.pot = this.pot + Number(this.allBet);
          this.allBet = 0;
          this.show();
        } else {
          this.turn = this.players[this.p % this.players.length].name;
          console.log(this.turn + "の番");
        }
      } else {
        this.nextBr();
      }
    },
    nextBr() {
      //ベットをポットに移動
      for (let i = 0; i < this.players.length; i++) {
        this.allBet = Number(this.allBet) + Number(this.players[i].bet);
        this.players[i].bet = 0;
      }
      this.pot = this.pot + Number(this.allBet);
      this.allBet = 0;
      if (this.br < 3) {
        //ベッティングラウンドを進める
        this.br = this.br + 1;
        console.log(this.bettingRound[this.br]);
        this.p = this.round % this.players.length;
        while (this.players[this.p].isplay != 0) {
          this.p++;
          if (this.p == this.maxRound + this.players.length) {
            break;
          }
        }

        this.turn = this.players[this.p % this.players.length].name;
        console.log(this.turn + "の番");
      } else {
        if (this.action != "フォールド") {
          this.show();
        }
      }
    },
    nextRound() {
      //次のラウンド・ディーラーを回す
      if (this.round < this.maxRound) {
        this.round += 1;
        this.pot = 0;
        this.br = 0;
        this.playing = this.players.length;
        for (let i = 0; i < this.players.length; i++) {
          if (this.players[i].chip > 0) {
            this.players[i].isplay = 0;
          } else {
            this.players[i].isplay = 1;
            this.playing--;
          }
          if (this.playing == 1) {
            this.sort();
            this.$store.commit("navigator/push", page3);
          }
        }
        this.p = (this.round - 1) % this.players.length;
        while (this.players[this.p % this.players.length].isplay == 1) {
          this.p++;
        }
        this.dealer = this.players[this.p % this.players.length].name;
        //コンソール出力
        console.log("ディーラー:" + this.dealer);
        console.log(this.bettingRound[0]);
        //SB.BBのベット
        this.sbbb();
      } else {
        this.sort();
        //結果のページに遷移
        this.$store.commit("navigator/push", page3);
      }
    },
    sbbb() {
      //sb
      this.p = this.round % this.players.length;
      while (this.players[this.p % this.players.length].isplay != 0) {
        this.p++;
      }
      this.players[this.p % this.players.length].bet = this.minBet / 2;
      this.players[this.p % this.players.length].chip =
        this.players[this.p % this.players.length].chip -
        this.players[this.p % this.players.length].bet;
      console.log(this.players[this.p % this.players.length].name + "の番");
      console.log("ベット額:" + this.players[this.p % this.players.length].bet);
      console.log("チップ:" + this.players[this.p % this.players.length].chip);
      //bb
      this.p++;
      while (this.players[this.p % this.players.length].isplay != 0) {
        this.p++;
      }
      this.players[this.p % this.players.length].bet = this.minBet;
      this.players[this.p % this.players.length].chip =
        this.players[this.p % this.players.length].chip -
        this.players[this.p % this.players.length].bet;
      console.log(this.players[this.p % this.players.length].name + "の番");
      console.log("ベット額:" + this.players[this.p % this.players.length].bet);
      console.log("チップ:" + this.players[this.p % this.players.length].chip);
      //utg
      this.p++;
      while (this.players[this.p % this.players.length].isplay != 0) {
        this.p++;
      }
      this.turn = this.players[this.p % this.players.length].name;
      console.log(this.players[this.p % this.players.length].name + "の番");
    },
    show() {
      this.$modal.show("winner-modal");
    },
    sort() {
      console.log(this.players);
      this.players.sort(function(a, b) {
        if (a.chip > b.chip) return -1;
        if (a.chip < b.chip) return 1;
        return 0;
      });
      this.$store.state.ranking = this.players;
    },
    fetch() {
      fetch(this.$url + "/ranking", {
        method: "GET"
      })
        .then(function(response) {
          if (response.status == 200) {
            return response.json();
          }
          // 200番以外のレスポンスはエラーを投げる
          return response.json().then(function(json) {
            throw new Error(json.message);
          });
        })
        .then(function(json) {
          // レスポンスが200番で返ってきたときの処理はここに記述する
          console.log(json);
          this.records = json.records;
          console.log(this.records);
        })
        .catch(function(err) {
          // レスポンスがエラーで返ってきたときの処理はここに記述する
        });
    }
  }
};
</script>


<style scoped>
</style>



